import React, { useState, useEffect } from "react";
import socketIOClient from "socket.io-client";
import "./App.css";
import { useNiconico } from "react-niconico";
var socket;

socket = socketIOClient();
const data = { nivel: "122", username: ".Aracely.", text: "Lol", src: null };
function App() {
    const [ref, emitText] = useNiconico();
    const [socketID, setSocketID] = useState("");
    useEffect(() => {
        socket.on("connect", function () {
            console.log("Successfully connected!");
            setSocketID(socket.id);
        });
        socket.on("roomJoined", (msg) => {
            console.log("roomJoined", msg);
            emitText(msg.username + " " + msg.text);
        });
    }, []);

    return <div className="App App-header" ref={ref}></div>;
}

export default App;
